import MpPicker from '@/third/MpPicker/mpvuePicker.vue';
import {
	regionData
} from '@/data/region';
import {
	mapActions,
	mapGetters
} from 'vuex'
import {
	updateUserInfo
} from '@/api/user'
import formValidate from '@/utils/validate'
export default {
	components: {
		MpPicker
	},
	data() {
		return {
			//国籍
			nationArr: ['中国', '越南', '缅甸', '泰国', '老挝'],
			nationIndex: 0,
			//证件类型
			idTypeArr: ['身份证', '边民证', '护照', '港澳通行证'],
			idTypeIndex: 0,
			formData: {},
			//隶属海关
			regionPickerArry: regionData,
			regionDef: [0, 0],
			regionText: ''
		}
	},
	onLoad() {
		//解构赋值
		this.formData = { ...this.userInfo
		}
		this.init();
	},
	computed: {
		...mapGetters({
			userInfo: 'user/userInfo'
		})
	},
	methods: {
		...mapActions({
			setUserInfo: 'user/setUserInfo'
		}),
		handleUpdate() {
			//验证规则
			let rule = [{
					name: 'name',
					checkType: 'notnull',
					errorMsg: '请填写姓名'
				},
				{
					name: 'sex',
					checkType: 'notnull',
					errorMsg: '请选择性别'
				},
				{
					name: 'nationality',
					checkType: 'notnull',
					errorMsg: '请选择国籍'
				},
				{
					name: 'idType',
					checkType: 'notnull',
					errorMsg: '请选择证件类型'
				},
				{
					name: 'idCode',
					checkType: 'notnull',
					errorMsg: '请填写证件号码'
				},
				{
					name: 'idCode',
					checkType: 'string',
					checkRule: '18',
					errorMsg: '证件号不合法'
				},
				{
					name: 'customs',
					checkType: 'notnull',
					errorMsg: '请选择隶属海关'
				},
			];

			var valid = formValidate.check({ ...this.formData
			}, rule);
			if (valid) {
				updateUserInfo(this.formData).then(res => {
					let {
						code,
						msg,
						data
					} = res;
					if (code === 500) {
						uni.showToast({
							title: msg,
							icon: 'none'
						})
					}

					if (code === 200) {
						uni.showToast({
							title: '更新成功',
							success: () => {
								uni.setStorageSync('userInfo', data);
								this.setUserInfo(data)
								this.$Router.pushTab("/pages/user/center")
							}
						});
					}

				})
			} else {
				uni.showToast({
					title: formValidate.error,
					icon: 'none'
				});
			}
		},
		//性别选择
		flagRadioChange({
			detail
		}) {
			this.formData.sex = detail.value;
		},
		//国籍选择
		pickerNationalityChange(e) {
			this.nationIndex = e.target.value;
			this.formData.nationality = this.nationArr[this.nationIndex];
		},
		//证件类型选择
		pickerIdTypeChange(e) {
			this.idTypeIndex = e.target.value;
			this.formData.idType = this.idTypeArr[this.idTypeIndex];
		},
		//关区选择
		regionConfirm(e) {
			this.formData.customs = e.value[0];
			this.formData.region = e.value[1];
			this.regionText = e.label;
		},
		//显示隶属关区 联动列表
		showRegion() {
			this.hideKeyBoard();
			this.$refs.regionPicker.show();
		},
		//隐藏键盘
		hideKeyBoard() {
			uni.hideKeyboard();
		},
		//初始化默认选项值
		init() {
			let {
				customs,
				region,
			} = this.userInfo;
			let regionText = ''
			//隶属关区
			if (customs != '' && customs != null) {
				let regionDef = [];
				regionData.forEach((item, index, arr) => {
					if (customs === item.value) {
						regionDef.push(index);
						regionText = item.label;
						item.children.forEach((cItem, cIndex, cArr) => {
							if (cItem.value === region) {
								regionDef.push(cIndex);
								regionText += '-' + cItem.label;
							}
						});
					}
				});
				this.regionText = regionText;
				this.regionDef = regionDef;
			}
		}
	}
}
